@import "icons-variables";

@font-face {
    font-family: 'o-icon';
    src: url('#{$ygg-font-path}/icon.eot?eo25wp');
    src: url('#{$ygg-font-path}/icon.eot?eo25wp#iefix') format('embedded-opentype'),
    url('#{$ygg-font-path}/icon.ttf?eo25wp') format('truetype'),
    url('#{$ygg-font-path}/icon.woff?eo25wp') format('woff'),
    url('#{$ygg-font-path}/icon.svg?eo25wp#o-icon') format('svg');
    font-weight: normal;
    font-style:  normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family:             'o-icon' !important;
    speak:                   none;
    font-style:              normal;
    font-weight:             normal;
    font-variant:            normal;
    text-transform:          none;
    line-height:             1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing:  antialiased;
    -moz-osx-font-smoothing: grayscale;
}


